body {
  background-color: whitesmoke;
}
.ul {
  position: absolute;
  right: 0;
}
.myNavbar {
  border-bottom: 1px solid rgba(168, 163, 163, 0.698);
  background-color: white;
}
.logo {
  width: 7rem;
}
.icon {
  font-size: 1.8em;
}
.PostImage {
  margin-left: 235px;
  text-align: left;
}
.profilePic,
.activityImg {
  border-radius: 50%;
  border: 1px solid rgba(168, 163, 163, 0.698);
  width: 8rem;
  padding: 0;
  margin-left: 2rem;
}
.details {
  width: 60%;
  margin: 5px 0px 5px 30px;
}
.profileName {
  font-weight: normal;
}
.myDetails {
  border-bottom: 1px solid rgba(168, 163, 163, 0.698);
}
.myFooter {
  left: 0;
  bottom: 0;
  right: 0;
}
.footerLink {
  color: rgba(168, 163, 163, 0.945);
  font-size: 0.8rem;
}
.Images {
  margin: 14px;
  height: 17rem;
  width: 17rem;
}
.activity {
  background-color: white;
  border: 1px solid rgba(168, 163, 163, 0.698);
  border-radius: 5px;
}
.myActivity {
  font-size: 1em;
  width: 92%;
}
.dark {
  color: rgba(168, 163, 163, 0.945);
}
.activityImg {
  height: 4rem;
  width: 4rem;
  margin-left: 0;
}
.row {
  display: flex;
  flex-wrap: wrap;
  padding: 0 4px;
}
.column {
  flex: 25%;
  max-width: 25%;
  padding: 0 4px;
}
.column img {
  margin-top: 8px;
  vertical-align: middle;
  width: 100%;
}
.newPost {
  width: 320px !important;
}
/* ------------------Responsive Code--------------- */
@media (max-width: 800px) {
  .column {
    flex: 50%;
    max-width: 50%;
  }
}
@media (max-width: 600px) {
  .explore {
    padding: 20px !important;
  }
}

@media (max-width: 1372px) {
  .PostImage {
    margin-left: 200px;
  }
  .myActivity {
    font-size: 0.9em;
    width: 75%;
  }
}
@media (max-width: 1330px) {
  .PostImage {
    margin-left: 165px;
  }
}
@media (max-width: 1277px) {
  .PostImage {
    margin-left: 125px;
  }
}
@media (max-width: 1160px) {
  .PostImage {
    margin-left: 110px;
  }
  .Images {
    height: 15rem;
    width: 15rem;
  }
}
@media (max-width: 1075px) {
  .PostImage {
    margin-left: 100px;
    margin-bottom: 160px;
  }
  .Images {
    height: 14rem;
    width: 14rem;
  }
}
@media (max-width: 991px) {
  .whiteDiv {
    width: 90%;
  }
  .navbar-nav {
    flex-direction: row !important;
  }
  a.nav-link {
    padding: 8px !important;
  }
  .profilePic {
    margin-left: 40px;
  }
  label.mt-2.ms-5 {
    margin-left: 15px !important;
    font-size: 12px;
  }
  .numOfPosts,
  label.mt-2,
  label.mt-1 {
    font-size: 12px;
  }
  .details {
    width: 55%;
  }
  .Images {
    height: 10.5rem;
    width: 10.5rem;
  }
  .PostImage {
    margin-left: 140px;
  }
}
@media (max-width: 897px) {
  .Images {
    height: 10.2rem;
    width: 10.2rem;
  }
  .PostImage {
    margin-left: 100px;
  }
}
@media (max-width: 827px) {
  .Images {
    height: 10rem;
    width: 10rem;
  }
  .PostImage {
    margin-left: 80px;
  }
}
@media (max-width: 768px) {
  .PostImage {
    margin-bottom: 130px;
  }
  .Images {
    height: 9.8rem;
    width: 9.8rem;
    margin: 10px;
  }
  .PostImage {
    margin-left: 70px;
  }
}
@media (max-width: 720px) {
  .Images {
    height: 9rem;
    width: 9rem;
    margin: 8px;
  }
  .PostImage {
    margin-left: 65px;
  }
  .profilePic {
    margin-left: 30px;
    width: 6rem;
    height: 6rem;
  }
  .profileName {
    font-size: calc(1rem + 0.6vw);
    margin-bottom: 0;
  }
  label.mt-2.ms-5 {
    margin-left: 10px !important;
  }
  label.mt-2.ms-5,
  .numOfPosts,
  label.mt-2,
  label.mt-1 {
    font-size: 9px;
    margin-top: 0px !important;
  }
  .smallDeviceWidth {
    width: 100%;
  }
  .myActivity {
    width: 85%;
  }
}
@media (max-width: 650px) {
  .Images {
    height: 9rem;
    width: 9rem;
  }
  .PostImage {
    margin-left: 30px;
    width: 100%;
  }
  .myActivity {
    font-size: 0.8em;
    margin-top: 1.15rem !important;
  }
  .activityImg {
    height: 3rem;
    width: 3rem;
  }
}
@media (max-width: 586px) {
  .Images {
    height: 8.4rem;
    width: 8.4rem;
    margin: 5px;
  }
  .PostImage {
    margin-left: 20px;
  }
  .profilePic {
    margin-left: 15px;
    width: 5.5rem;
    height: 5.5rem;
  }
  .details {
    width: 61%;
  }
  .myActivity {
    margin-top: 0.75rem !important;
  }
  .inputImg {
    padding-left: 0 !important;
    font-size: 0.8em;
  }
}
@media (max-width: 510px) {
  .Images {
    height: 7rem;
    width: 7rem;
    margin: 4px;
  }
  .PostImage {
    margin-left: 18px;
  }
  label.mt-2.ms-5,
  .numOfPosts,
  label.mt-2,
  label.mt-1 {
    font-size: 7.5px;
  }
  .myActivity {
    font-size: 0.6rem;
    width: 80%;
  }
  .details {
    width: 63%;
    margin-left: 15px !important;
  }
  .profilePic {
    margin-left: 0;
    width: 5rem;
    height: 5rem;
  }
  .profileName {
    font-size: calc(0.8rem + 0.6vw);
  }
  .footerLink {
    font-size: 0.5rem;
  }
}
@media (max-width: 452px) {
  .Images {
    height: 6.5rem;
    width: 6.5rem;
    margin: 3px;
  }
  .PostImage {
    margin-left: 6px;
  }
}
@media (max-width: 418px) {
  .PostImage {
    margin-bottom: 130px;
  }
  .Images {
    height: 6rem;
    width: 6rem;
    margin: 2px;
  }
  .PostImage {
    margin-left: 6px;
  }
  .profilePic {
    width: 4.5rem;
    height: 4.5rem;
  }
  .details {
    margin-left: 8px !important;
    width: 67%;
  }
  .icon {
    font-size: 1.2em;
  }
  .logo {
    width: 4.8rem;
  }
  .inputImg {
    margin-left: 0 !important;
  }
  .row.mt-5.mb-5.ms-2.text-center {
    margin-top: 20px !important;
  }
  .myDetails {
    padding-bottom: 20px !important;
  }
  .col-10.col-lg-8.col-xlg-8.col-md-8.col-sm-8.mx-auto.mt-5 {
    margin-top: 20px !important;
  }
}
@media (max-width: 370px) {
  .Images {
    height: 6rem;
    width: 6rem;
    margin: 1px;
  }
  .PostImage {
    margin-left: 8px;
    margin-bottom: 60px;
    padding: 0%;
  }
  .row.mt-5.mb-5.ms-2.text-center {
    margin-left: 0 !important;
  }
  .profilePic {
    width: 4rem;
    height: 4rem;
  }
  .myActivity {
    padding-right: 0;
    font-size: 0.5rem;
    width: 78%;
  }
}
@media (max-width: 336px) {
  .postMainContainer {
    padding: 0;
  }
  .details {
    padding-right: 0;
    padding-left: 1px;
    width: 68%;
  }
  .activityImg {
    height: 2.5rem;
    width: 2.5rem;
  }
  .monthORweek {
    margin-top: 20px !important;
  }
  .inputImg {
    font-size: 0.5em;
  }
  .btn {
    font-size: 0.6rem;
  }
}
@media (max-width: 312px) {
  .icon {
    font-size: 1em;
  }
  a.nav-link {
    padding: 0.35rem !important;
  }
  .Images {
    height: 5.5rem;
    width: 5.5rem;
  }
  .PostImage {
    margin-left: 4px;
  }
  .profilePic {
    width: 3.5rem;
    height: 3.5rem;
  }
  .profileName {
    font-size: calc(0.7rem + 0.6vw);
  }
  label.mt-2.ms-5,
  .numOfPosts,
  label.mt-2,
  label.mt-1 {
    font-size: 7px;
  }
}
